*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

html {
  scroll-behavior: smooth;
}
:root {
  --color-pri-1: hsl(35, 77%, 62%);
  --color-pri-2: hsl(5, 85%, 63%);
  --color-pri-3: hsl(240, 100%, 5%);
  --main-font: "Inter", sans-serif;
  --pri-font-color: hsl(236, 13%, 42%);
}
body {
  background-color: hsl(36, 100%, 99%);
  margin: 0 5%;
}
/* navabar styling */

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem;
}

.nav .headline {
  font-family: var(--main-font);
  font-size: xx-large;
  font-weight: 800;
}

.nav ul {
  color: var(--pri-font-color);
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  list-style-type: none;
  font-weight: bold;
  gap: 5%;
}

ul li:last-child {
  margin-right: 10%;
}

/* main body */
.body-template {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: repeat(3, 1fr);
  gap: 2%;
  margin-bottom: 1rem;
}

.grid:nth-child(1) {
  grid-column: 1 / span 2;
}

.grid:nth-child(2),
.grid:nth-child(3) {
  grid-row: 2;
}

.body-template .note {
  background-color: var(--color-pri-3);
  color: white;
  padding: 1rem;
  grid-column: 3;
  grid-row: 1 / span 2;
}
.note h2 {
  color: var(--color-pri-1);
  font-size: 2em;
  margin-bottom: 1%;
}
.note p {
  font-size: 12px;
  color: var(--pri-font-color);
  /* text-align: center; */
  width: 10rem;
}

main h1 {
  color: hsl(240, 100%, 5%);
  font-family: var(--main-font);
  font-size: 50px;
  line-height: 1;
  font-weight: bolder;
}
.body-template .acknowlegement {
  color: var(--pri-font-color);
}
.body-template .acknowlegement .button {
  background-color: var(--color-pri-2);
  padding: 0.5rem 1.5rem;
}

hr {
  border: 1px solid hsl(236, 13%, 42%);
  margin-bottom: 3rem;
  width: 20rem;
}

/* last part */

.last-part {
  display: flex;
  margin: 5% 0 3%;
}

.last-part img {
  height: 100px;
}

.last-part .child {
  counter-increment: counter-name;
  display: flex;
  flex-direction: row;
  gap: 3%;
}
.last-part h4::before {
  content: "0" counter(counter-name);
  color: hsl(236, 13%, 42%);
  display: block;
  font-size: 1.4em;
}

.hidden {
  display: none;
}

.nav-toggle-1 {
  display: none;
}

@media (max-width: 684px) {
  .nav ul {
    display: none;
  }

  .nav .nav-js {
    /* display: inline-flex; */
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    left: 40%;
    bottom: 10%;
    flex-direction: row;
    justify-content: space-between;
  }

  .nav.ul {
    direction: flex;
    flex-direction: row;
  }
  .body-template {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-bottom: 10%;
  }

  .last-part {
    display: grid;
    gap: 1.5rem;
    margin-top: 3em;
  }
}
